.ordersContainer .rs-table-cell-header .rs-table-cell-content {
    line-height: 1.66666667;
    padding: 10px;
    font-size: 16px;
    font-weight: 600;
}

.ordersContainer .MuiInputBase-root.MuiOutlinedInput-root{
    height:40px;
    width:180px;
}

.ordersContainer .MuiFormControl-root{
    margin-top:0px !important;
    margin-bottom: 0px !important;
    width:180px;
}

.ordersContainer .rs-table-cell-content{
    color:#000;
}

.ordersContainer .rs-table-row:nth-child(odd) .rs-table-cell {
    background-color: #F8F9FA;
}

.ordersContainer input.nosubmit {
    border: 1px solid #555;
    border-radius: 4px;
    height: 38px;
}

.ordersDownload{
    border:1px solid #e0e0e0;
    background:#000;
    color:#fff;
    border-radius:4px;
    height:38px;
}

.valueTable table{
    border:1px solid #000;
}
.valueTable th{
    padding:5px;
    text-align: center;
}

.valueTable td{
    border:1px solid #000;
    padding:5px;
}

.ordersContainer .title{
    font-family: 'TabarroPro' !important;
}